import { ModelProxy } from 'modelproxy';
import { InterfaceFactory } from 'modelproxy/out/libs/interface.factory';
import { IInterfaceModel } from 'modelproxy/out/models/interface';

import { fetchEngine, fetchSchemaEngine } from './engines';


/**
 * 
 * fetchjsonp
 */
const proxy = new ModelProxy();
// let state = 'dev';

// if (__PROD__) {
//     state = 'prd';
// }

// if (__STAG__) {
//     state = 'stg';
// }

proxy.loadConfig({
  "key": "test",
  "title": "p-uc",
  // tslint:disable-next-line:object-literal-sort-keys
  "engine": "default",
  "mockDir": "/mocks/",
  "states": {
    "dev": "http://www.baidu.com"
  },
  "state": "dev",
  "interfaces": [{
    "key": "article",
    "title": "",
    // tslint:disable-next-line:object-literal-sort-keys
    "method": "GET",
    "path": "/articles"
  }, {
    "key": "user",
    "title": "",
    // tslint:disable-next-line:object-literal-sort-keys
    "method": "GET",
    "path": "/users",
    "engine": "default"
  }]
}, {});

proxy.loadConfig({
  "engine": "json",
  "interfaces": [{
    "key": "schema",
    "method": "get",
    "path": "/static/schemas/:id",
    "title": "schema"
  }, {
    "key": "echarts",
    "method": "get",
    "path": "/static/echarts/:id",
    "title": "echart"
  }],
  "key": "sf",
  "state": "dev",
  "states": {
    "dev": ""
  },
  "title": "schema"
}, {});

// 
proxy.addEngines({
  fetch: fetchEngine,
  schema: fetchSchemaEngine
});

// 
const schemaNs = proxy.getNs("sf") as InterfaceFactory;
// json
export const getSchema = schemaNs.get("schema") as IInterfaceModel;

export default proxy;



